g to corruption. The world continues waiting for the true hero, and the doom that comes with.\n  ")]),_c('p',[_vm._v("\n    Those who have tried to expand upon the worlds knowledge are met with a horrid fate. Those to delve into magic are consumed by it, either killed or driven mad. Those who attempt to advance technology meet with similar results. The world has not evolved much since the prophet left. Innovation is stagnant, witchcraft is forbidden in all parts of the world, and inventors are despised, or worse.\n  ")]),_c('p',[_vm._v("\n    But this world is mostly peaceful. No great evils threaten the world, and mythical beasts are just that, myth. Political discourse is mostly positive throughout the world, save for the barbaric Sesta, and the lawless lands of Astipan. It is exactly as the prophet foretold. Which makes the world even more cautious about the prophecy of doom that the Sesta are hiding.\n  ")]),_c('h2',[_vm._v("Kingdom Descriptions")]),_c('h3',[_vm._v("Estar")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Labour, workers"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Monarchy"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Jalen Raldrud"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Ochar, Astipan, Malthein"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Sesta"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 4 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sororism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Estapor"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorores Blade. A sword that was supposedly made for Sorore, who supposedly carried it, but never used it."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Estar is a very traditional kingdom of a King, Queen, Lords, and knights. They most closely follow the tenants of order. Estar attempts to be the enforcers of the world, when you need help or a hero, you go to Estar. They share a border with the barbaric Sesta, and are often in conflicts with them.\n   \n  "),_c('h3',[_vm._v("Malthein")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Salt, Minerals"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Elder council"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Sasha Lao"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Estar, Frugua, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 300,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Soroism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Titans Heart"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorores Podium. A unique geological location in the mountains that create a massive echo effect to give grand speeches to the open area below."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" The people of Malthein lead a more reclusive lifestyle. The live among and even inside the mountains of Malthein. The follow Soroism but only mildly, and place more emphasis on wisdom and decision making. They travel the mountains regularly on spiritual quests of enlightenment.\n\n  "),_c('h3',[_vm._v("Sesta")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Slaves"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Leader completes a trail set by the previous leader"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Currently Undecided"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Astipan, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Frugua, Estar, Chechniar, Malthein"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 1 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sornal (Named after the city)"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Sornal, The Silent City"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorores tablet, which contains t