s take them in. The silver hair of savior is quite distinct, and no hair dye or magic has been able to replicate its appearance. Anyone who has seen a true silver haired savoir would easily be able to tell the difference.")]),_c('h3',[_vm._v("Religions")]),_c('p',[_vm._v("There are only 3 religions in the world, described below.")]),_c('p',[_vm._v("Sororism  The most common religion in Altasoror. It is a general worship of the creator and following the prophets will. There are no required church meetings, tithes or rituals.")]),_c('p',[_vm._v("Soric -  A far more organized religion that Sororism. The soric church governs over Chechniar, and all its inhabitants are required to pay a monthly tithe to the church. There is a very set organizational hierarchy, with the Sorom being the religious and governmental leader of the country.")]),_c('p',[_vm._v("Sornal  Only followed by the Sesta. They believed the teachings of the prophet would lead to the doom of the world. As such, they do everything in their power to disregard and work against the prophets will and those that would follow them.")])])}]

